import axios from "axios";
import website from "../config/webstie";
const columns = [
  {
    dataIndex: "Name",
    key: "Name",
    slots: { title: "customTitle" },
    scopedSlots: { customRender: "name" },
  },
  {
    title: "NamespaceId",
    dataIndex: "NamespaceId",
    key: "NamespaceId",
  },
  {
    title: "addr",
    dataIndex: "Ins",
    key: "Ins",
    scopedSlots: { customRender: "name" },
  },
  {
    title: "CheckNums",
    key: "CheckNums",
    dataIndex: "CheckNums",
  },
  {
    title: "CheckTime",
    key: "CheckTime",
    dataIndex: "CheckTime",
  },
  {
    title: "Action",
    key: "action",
    scopedSlots: { customRender: "action" },
  },
];

const data = [
  {
    key: "1",
    name: "John Brown",
    age: 32,
    address: "New York No. 1 Lake Park",
    tags: ["nice", "developer"],
  },
  {
    key: "2",
    name: "Jim Green",
    age: 42,
    address: "London No. 1 Lake Park",
    tags: ["loser"],
  },
  {
    key: "3",
    name: "Joe Black",
    age: 32,
    address: "Sidney No. 1 Lake Park",
    tags: ["cool", "teacher"],
  },
];

export default {
  data() {
    return {
      data,
      showerr: false,
      res: [],
      columns,
      size: "default",
      expand: false,
      result: "",
      sql: "",
      form: this.$form.createForm(this, { name: "advanced_search" }),
    };
  },
  computed: {
    count() {
      return this.expand ? 11 : 7;
    },
  },
  updated() {},
  mounted() {
    this.getServiceList();
  },
  methods: {
    onClose(e) {
      e.preventDefault();
      this.showerr = false;
    },
    onChange(e) {
      console.log("size checked", e.target.value);
      this.size = e.target.value;
    },
    getServiceList() {
      axios.get(website.URL + "service/list").then((res) => {
        if (res.data.code == 200) {
          this.res = res.data.data;
        }
      });
    },
    detail(s) {
      console.log(s);
      window.open("http://" + s.Ins.Ip + ":" + s.Ins.Port);
    },
    handleSearch(e) {
      e.preventDefault();
    },

    handleReset() {
      this.sql = "";
    },

    toggle() {
      this.expand = !this.expand;
    },
  },
};
</script>
